<template>
  <div class="faultreporting_add">
    <div class="box first arrow">
      <label class="row input_row" for="break_type">
        <div class="left">
          故障类型:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.category}">
          <span>{{this.formData.category ? this.formData.category.break_type: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="category" id="break_type" v-model="formData.category">
            <option value=""></option>
            <option :value="item" v-for="item in breakTypes" :key="item.id">{{item.break_type}}</option>
          </select>
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="device_name">
        <div class="left">
          设备名称:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.deviceName}">
          <span>{{this.formData.deviceName ? this.formData.deviceName: '请输入设备名称'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <input type="text" v-model="formData.deviceName" id="device_name">
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="device_no">
        <div class="left">
          设备编号:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.deviceNo}">
          <span>{{this.formData.deviceNo ? this.formData.deviceNo: '请输入设备编号'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <input type="text" v-model="formData.deviceNo" id="device_no">
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="device_line">
        <div class="left">
          所在线路:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.deviceLine}">
          <span>{{this.formData.deviceLine ? this.formData.deviceLine.name: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="deviceLine" id="device_line" v-model="formData.deviceLine">
            <option value=""></option>
            <option :value="item" v-for="item in lines" :key="item.id">{{item.name}}</option>
          </select>
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="device_position">
        <div class="left">
          所在位置:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.devicePosition}">
          <span>{{this.formData.devicePosition ? this.formData.devicePosition.name: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="devicePosition" id="device_position" v-model="formData.devicePosition">
            <option value=""></option>
            <option :value="item" v-for="item in stations" :key="item.id">{{item.name}}</option>
          </select>
        </div>
      </label>
    </div>
    <div class="box second">
      <div class="row">
        故障描述:
      </div>
      <div class="hr"></div>
      <div class="row textarea_box">
        <textarea name="" id="" rows="10" v-model="formData.desc" placeholder="请输入故障描述"></textarea>
      </div>
    </div>
    <div class="box third">
      <div class="row">
        照片:
      </div>
      <div class="hr"></div>
      <div class="row img_box">
        <upload :max="9" :handleClick="true" size="small" :token="imgUpToken" uploadUrl="http://up-z1.qiniu.com" :images="formData.imgs" @add-image="selectImg" @remove-image="rmImg"/>
      </div>
    </div>
    <common-btn class="add_btn" @click="commitChange">确认提交</common-btn>
    <div class="blank"></div>
  </div>
</template>

<script>
export default {
  name: 'FaultReportingAdd',
  data () {
    return {
      imgUpToken: '',
      lines: [],
      stations: [],
      breakTypes: [],
      formData: {
        category: '',
        deviceName: '',
        deviceNo: '',
        deviceLine: '',
        devicePosition: '',
        desc: '',
        imgs: []
      }
    }
  },
  computed: {
    sendee () {
      if (this.formData.sendee && this.formData.sendee.length > 0) {
        return this.formData.sendee.map((item) => {
          return item.name
        }).join(',')
      }
    }
  },
  methods: {
    selectImg () {
      let that = this
      // eslint-disable-next-line
      dd.biz.util.uploadImage({
        compression: false,
        quality: 100,
        resize: 100,
        stickers: {
          time: new Date()
        },
        onSuccess: function (result) {
          that.formData.imgs.push({ url: result[0] })
        },
        onFail: function (err) {
          alert(JSON.stringify(err))
        }
      })
    },
    rmImg () {
      this.formData.imgs.splice(0, 1)
    },
    commitChange () {
      console.log(this.formData)
      this.$rpc.call('funenc_xa_station.break_submit', 'save_break_submit', [], {
        vals: {
          break_type: this.formData.category && this.formData.category.id,
          equipment_name: this.formData.deviceName,
          equipment_number: this.formData.deviceNo,
          break_describe: this.formData.desc,
          line_id: this.formData.deviceLine && this.formData.deviceLine.id,
          site_id: this.formData.devicePosition && this.formData.devicePosition.id,
          url: JSON.stringify(this.formData.imgs)
        }
      }).then(res => {
        if (res) {
          this.$router.push({
            path: '/faultreporting'
          })
        }
      }).catch(e => {
        console.log(e)
        alert('提交失败')
      })
    },
    getLine () {
      this.$rpc.call('cdtct_dingtalk.cdtct_dingtalk_department', 'get_line_id', [], {}).then(res => {
        this.lines = res
      }).catch(e => {
        console.log(e)
        alert('获取线路失败')
      })
    },
    getSites (id) {
      this.$rpc.call('cdtct_dingtalk.cdtct_dingtalk_department', 'get_sites', [], {
        line_id: id
      }).then(res => {
        this.stations = res
      }).catch(e => {
        console.log(e)
        alert('获取地址失败')
      })
    },
    getBreakType () {
      this.$rpc.call('funenc_xa_staion.break_type_increase', 'get_break_type', [], {}).then(res => {
        this.breakTypes = res
        console.log(this.breakTypes)
      }).catch(e => {
        console.log(e)
        alert('获取故障类型失败')
      })
    }
  },
  mounted () {
    this.getLine()
    this.getBreakType()
  },
  watch: {
    'formData.deviceLine': {
      handler (v) {
        this.getSites(v.id)
      }
    }
  }
}
</script>

